import React from 'react'
import PropTypes from 'prop-types'
import { FormattedMessage } from 'react-intl'
import Layout from '../components/Layout'
import AppstoreIcon from  "../images/appstore-icon.png"
import PanelImageOne from "../images/panel-image-one.png"
import PlaystoreIcon from "../images/playstore-icon.png"
import HowToPhones from "../images/how-to-phones.png"
import GetPremiumPortalBottom from "../images/get-premium-portal-bottom.png"
import FaqsMobileHeader from "../images/premium-faqs-header-mobile-image.jpg"
import MediaQuery from 'react-responsive';


import Helmet from "react-helmet";
import { withPrefix, Link } from "gatsby"
import SEO from '../Components/seo'


const FaqsPage = ({ pageContext: { locale } }) => (
  <Layout locale={locale}>
    <SEO title="The 2019 Watty Awards - Wattpad" keywords={[`Wattys`, `Wattpad`]} />

    <div class="section white-bg" id="faqs-header-section" >

    <MediaQuery maxWidth={700}>
              {(matches) => {
                if (matches) {
                  return ( 
                    <img src={FaqsMobileHeader}/>
                  ); 
               
                } else {
                    return null
                }
              }}
     </MediaQuery>

    <div className="section-inner h-center" >



            <MediaQuery maxWidth={700}>
              {(matches) => {
                if (matches) {
                  return ( 
                    <p className="font-xlarge regular-txt charcoal-txt pad-tb">
                    Frequently Asked Questions 
                    </p>                  
                ); 
               
                } else {
                    return (
                        <p className="font-xlarge regular-txt charcoal-txt">
                        Frequently Asked Questions 
                        </p>   
                    )
                }
              }}
     </MediaQuery>


    </div>
    </div>

    <div class="section white-bg pad-xb" id="how-to-steps" >
        <div className="section-inner">
    
            <div className="flex">
     
                <div className="col">
                    <p className="regular-txt charcoal-txt question pad-stsb">What is Wattpad Premium?</p>
                    <div className="answer-wrapper">
                        <p className="light-txt black-txt pab-b">Wattpad Premium is an enhanced, subscription-based version of Wattpad. It’s the same Wattpad you know and love, supercharged. Premium subscribers receive uninterrupted, ad-free reading across all their devices. Subscribers also receive bonus Coins—up to 66% more Coins free—with every purchase of any Wattpad Coin package to unlock Paid Stories. Another Premium feature? Access to custom theme colors to personalize the entire Wattpad experience. </p>
                    </div>
                    <p className="regular-txt charcoal-txt question pad-stsb">Why does Wattpad need ads?</p>
                    <div className="answer-wrapper">

                      <p className="light-txt black-txt pab-b">Since 2007, we’ve grown our incredible community to 70 million monthly users across the world. Doing that requires a lot of resources, technical infrastructure, and support.
                        As with many online platforms, we currently use ads to keep Wattpad going—you may have seen them around! We’ve also received many requests for an ad-free Premium option—standard with most apps these days. Wattpad Premium is our way of enabling our community to access an ad-free and enhanced reading experience, all while enabling us to continue our work of creating a home for our global community and their favorite stories. </p>
                    </div>
                    <p className="regular-txt charcoal-txt question pad-stsb">Will the free version of Wattpad go away?</p>
                    <div className="answer-wrapper">
                        <p className="light-txt black-txt pab-b">No, reading and writing on Wattpad will always be free. Whether you use the free version or enjoy Premium, Wattpad will always be where stories live. </p>
                    </div>
                    <p className="regular-txt charcoal-txt question pad-stsb">Why did Wattpad add Paid Stories and Wattpad Premium. Is Wattpad going to become a paid platform?</p>
                    <div className="answer-wrapper">
                        <p className="light-txt black-txt pab-b">
                        Paid Stories and Wattpad Premium are two completely different programs, but both were created with a mission to enhance the Wattpad experience for our community—whether you’re a reader or a writer. 

                        You can now enjoy Wattpad in 3 ways:
                        Read for Free: Enjoy Wattpad the way you always have. There are still hundreds of millions of free stories on Wattpad and that isn’t going to change.
                        Try Wattpad Premium: Subscribe for an enhanced reading experience with uninterrupted ad-free reading, access to bonus Coins for Paid Stories, and customized themes. 
                        Discover Paid Stories: Support Wattpad writers by purchasing Coins for access to exclusive stories. 
                        </p>
                    </div>
                    <p className="regular-txt charcoal-txt question pad-stsb">What do you get with Wattpad Premium?</p>
                    <div className="answer-wrapper">
                        <p className="light-txt black-txt pab-b">Wattpad Premium offers an enhanced reading experience. When you subscribe to Wattpad Premium, you’ll instantly be able to browse, read, and publish stories, without seeing ads.  Premium subscribers are also rewarded with bonus Coins with every purchase of any Coin package that can be used to unlock Paid Stories. You’ll also get access to fresh, exclusive colors to personalize your Wattpad experience. </p>
                    </div>
                    <p className="regular-txt charcoal-txt question pad-stsb">What are bonus Coins? </p>
                    <div className="answer-wrapper">
                        <p className="light-txt black-txt pab-b">Wattpad Premium subscribers get exclusive access to bonus Wattpad Coins when they purchase any Coin package. Depending on your Coin package purchase, as a subscriber, you can enjoy up to 66% more Coins to use to unlock any Paid Stories.  </p>
                    </div>
                    <p className="regular-txt charcoal-txt question pad-stsb">How do I purchase Wattpad Coins? </p>
                    <div className="answer-wrapper">
                        <p className="light-txt black-txt pab-b">You can purchase Coins in the Coin Shop on your Wattpad app. To visit the Coin Shop, click on the Coin balance listed on your profile. 
                            <br/>
                                As an added perk, as Wattpad Premium subscriber, you’ll receive bonus Coins with every Coin purchase. 
                            <br/>
                            Learn how to add Coins to your wallet on the <span className="orange-txt">Paid Stories website. </span>
                        </p>
                    </div>
                    <p className="regular-txt charcoal-txt question pad-stsb">Will I get bonus Coins monthly?</p>
                    <div className="answer-wrapper">
                        <p className="light-txt black-txt pab-b">You can purchase Coins in the Coin Shop on your Wattpad app. To visit the Coin Shop, click on the Coin balance listed on your profile. 
                        Premium subscribers have access to bonus Coins any time they choose to purchase a Coin package. However, subscribers do not get Coins automatically deposited into their wallet.                         </p>
                    </div>
                    <p className="regular-txt charcoal-txt question pad-stsb">How many bonus Coins do I receive with my Premium subscription?</p>
                    <div className="answer-wrapper">
                        <p className="light-txt black-txt pab-b">The number of Bonus Coins you receive depend on the Coin package you purchase. You will see the amount of bonus Coins available to you in the Coin Shop on your device. <br/>
                        To learn more about Wattpad Coins, visit our Paid Stories website: <a href="http://www.wattpad.com/paidstories" target="_blank">www.wattpad.com/paidstories</a>
                        </p>
                    </div>
                    <p className="regular-txt charcoal-txt question pad-stsb">I thought Wattpad Premium subscribers could enjoy unlimited access to Paid Stories? </p>
                    <div className="answer-wrapper">
                        <p className="light-txt black-txt pab-b">For a limited time, we were offering Premium subscribers the opportunity to try our Paid Stories at no extra cost. This limited Premium perk has now been retired. However, Premium subscribers can keep exploring Paid Stories with the opportunity to access bonus Coins when they purchase any Coin package. 
                        </p>
                    </div>
                    <p className="regular-txt charcoal-txt question pad-stsb">I’m a Wattpad Premium subscriber and I live in a country where Paid Stories are not available. How can I access Paid Stories now that I can’t enjoy unlimited access to Paid Stories?</p>
                    <div className="answer-wrapper">
                        <p className="light-txt black-txt pab-b">We’re actively working on rolling out Paid Stories globally by mid-to-late July. We ask for your patience until we are able to announce the launch of Paid Stories in your country. Until then, you will not be able to access any Paid Story in your geography, nor access the Coin Shop.
                        </p>
                    </div>
                    <p className="regular-txt charcoal-txt question pad-stsb">I was reading a Paid Story with the limited perk of full access to Paid Stories as a Premium subscriber. Do I still have access to this story? </p>
                    <div className="answer-wrapper">
                        <p className="light-txt black-txt pab-b">Any story part you’ve accessed and read that are behind the Paid Stories paywall will still be made available to you - if the Paid Story program is available in your country. Any unread story parts that are part of the Paid Stories program will now need Coins to access them. 
                        </p>
                    </div>
                    <p className="regular-txt charcoal-txt question pad-stsb">Why don’t Wattpad Premium subscribers get access to all Paid Stories?  </p>
                    <div className="answer-wrapper">
                        <p className="light-txt black-txt pab-b">Wattpad Premium and Wattpad Paid Stories are two distinct offerings. Premium is a subscription service that provides an enhanced reading experience to Wattpad users. 
Paid Stories is a program we developed to provide a way for readers to show monetary support for the writers they love. To learn more about Wattpad Paid Stories, click here. 
                        </p>
                    </div>
                    <p className="regular-txt charcoal-txt question pad-stsb">Can I use Wattpad Premium anywhere I use Wattpad?</p>
                    <div className="answer-wrapper">
                        <p className="light-txt black-txt pab-b">Yes. When you subscribe to Wattpad Premium you will automatically have access to Premium features wherever you use Wattpad. For example, if you purchase your subscription through your iTunes account, you will also be upgraded if you use the Android or web versions.
                        </p>
                    </div>
                    <p className="regular-txt charcoal-txt question pad-stsb">How do I purchase Wattpad Premium?</p>
                    <div className="answer-wrapper">
                        <p className="light-txt black-txt pab-b">You can only purchase Wattpad Premium from within the Wattpad Android or iOS apps. To upgrade, simply tap on the Go Premium button from the home screen, or select it in your Settings menu. Once you purchase Wattpad Premium, your account will instantly be upgraded wherever you use Wattpad—including in-app and on the web.  
                        </p>
                    </div>
                    <p className="regular-txt charcoal-txt question pad-stsb">If I subscribe to Wattpad Premium, am I no longer supporting writers who make money from ads?</p>
                    <div className="answer-wrapper">
                        <p className="light-txt black-txt pab-b">
                        Through Wattpad Premium, you are supporting writers, as your subscription helps Wattpad build features and programs that help writers succeed and empowers our community to connect and grow.
                        <br/>
                        Wattpad will always be free and accessible to everyone. And as with every new feature that we launch, we're closely monitoring how this affects our community, including the impact on how our writers make money.
                        </p>
                    </div>
                    <p className="regular-txt charcoal-txt question pad-stsb">Is the content different with Wattpad Premium?</p>
                    <div className="answer-wrapper">
                        <p className="light-txt black-txt pab-b">
                        No. Wattpad Premium offers an enhanced reading and writing experience within the same Wattpad you know and love through benefits like ad-free, access to bonus Coins, and exclusive color themes. 
                        <br/>
                        Wattpad Premium doesn’t mean premium content. The stories will remain the same, regardless of whether you are a free user or a Premium user.
                        </p>
                    </div>
                </div>
            </div>
        </div>
    </div>

  


    

  </Layout>
)

FaqsPage.propTypes = {
  pageContext: PropTypes.shape({
    locale: PropTypes.string.isRequired
  }).isRequired
}

export default FaqsPage